.floor-plan {
    max-width: 1440px;
    margin: 60px auto;
    .active {
        .sold {
         fill: #51694d;
        }
     }
}

.flat-info {
    display: flex;
    padding: 36px 24px;
}

.flat-item {
    padding: 20px;
    border: 1px solid;
    text-align: center;
    font-size: 20px;
    line-height: 1;
    display: grid;
    b {
        font-size: 24px;
        margin-top: auto;
        padding-top: 18px;
    }
}

.flat {
        .plan {
            fill: #03f41b00;

    }
 

    &.action{

            .plan:hover {
                fill: #d9f50685;
            }

    }

    &.sold{
            .plan:hover {
                fill: #ff000059;
            }
    }
    &.booking{

            .plan:hover {
                fill: #0400ff6f;
            }

    }
    &.active {
        .plan:hover {
            fill: #0c0c0c95;
            stroke: #cb5fb29d;
            stroke-width: 5px;
        }
    }
}

.plan {
    fill: #f0f8ff00;
    stroke: #ff000000;
    cursor: pointer;
    &:hover {
        fill: #74ff893c;
        stroke: #74ff893c;
        stroke-width: 3px;
    }

}

.flat-info {
    display: flex;
    padding: 36px 24px;
}

.floor-plan {
    max-width: 1440px;
    margin: 60px auto;
}

.flat-item {
    padding: 20px;
    border: 1px solid;
    text-align: center;
    font-size: 20px;
    line-height: 1;
    display: grid;
    b {
        font-size: 24px;
        margin-top: auto;
        padding-top: 18px;
    }
}

.flat {
    .bg-status {
        fill: #03a9f4;
    }
 

    &.action{
        .bg-status {
            fill: #80D34E;
        }
    }

    &.sold{
        .bg-status {
            fill: #D34E4E;
        }
    }
    &.booking{
        .bg-status {
            fill: #6B68FF;
        }
    }
    &.active {
        .plan {
            fill: #2b5c3295;
            stroke: #42fb5e;
            stroke-width: 5px;
        }
    }
}




