.template-plan {
    display: flex;
    flex-direction: row;

    svg {
        max-width: 800px;
        min-width: 350px;
        width: 100%;
    }
    .container {
        display: flex;
        flex-direction: row;
        justify-content: center;
    }
    .house-info {
        display: flex;
        flex-direction: column;
    }

}

