.house {
    stroke: rgba(255, 0, 0, 0);
    fill: rgba(242, 31, 8, 0);
    cursor: pointer;
    fill-opacity: 0.5;
    transition: .3s;

    &:hover{
        stroke: rgba(43, 226, 67, 0.445);
        fill: rgba(43, 226, 67, 0.445);
    }

    &:active {
        fill: rgba(43, 226, 67, 0.445);
    }
 }

.house-info {
    width: 500px;
    margin: 18px;
    border-radius: 25px;

    &__title {
        margin-bottom: 30px;
        text-align: center;
        font-weight: 800;
    }

    h4 {
        display: flex;
        margin-bottom: 10px;
        padding: 15px 15px;
        font-weight: 400;
        margin-left: 10px;
        margin-right: 10px;

        span {
            display: inline-block;
            margin-left: 25px;
            font-weight: 700;
            color: #998fe8eb;
        }
    }

    &__descriptions {
        padding: 15px 15px;
        font-weight: 600;
        min-height: 100px;
        height: 200px;
        overflow: auto;
        color: red;
        border-bottom-left-radius: 25px;
        border-bottom-right-radius: 25px;
    }
}
